/**
 * Call api or async inside Actions because Mutation only accept sync works
 */
import i18n from '@/i18n';
import api from '@/services/api.service';
import {RESPONSE_STATUS} from '@/constants';
import {DO_UPLOAD, DO_UPLOAD_SUCCESS, DO_UPLOAD_FAILED} from '@/mutations/Upload.mutation';

export default {
    [DO_UPLOAD]: async ({commit}, payload) => {
        commit(DO_UPLOAD);
        let res = await api.doUploadFile(payload);
        if (res && res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
            commit(DO_UPLOAD_SUCCESS, res.data.RST_DATA);
        } else {
            commit(DO_UPLOAD_FAILED);
        }
    },
}