import router from '@/router'

export const CREATE_CYNEWS = 'CREATE_CYNEWS';
export const FETCH_CYNEWS = 'FETCH_CYNEWS';
export const FETCH_CYNEWS_SUCCESS = 'FETCH_CYNEWS_SUCCESS';
export const FETCH_CYNEWS_FAILED = 'FETCH_CYNEWS_FAILED';

export const FETCH_CYNEW_DETAIL = 'FETCH_CYNEW_DETAIL';
export const FETCH_CYNEW_SUCCESS = 'FETCH_CYNEW_SUCCESS';
export const FETCH_CYNEW_FAILED = 'FETCH_CYNEW_FAILED';

export const CYNewMutation = {
    [CREATE_CYNEWS]: function (state) {
        state.CYNews = [];
    },
    [FETCH_CYNEWS_SUCCESS]: function (state, payload) {
        state.CYNews = [...state.CYNews, ...payload.RST_DATA];
        state.totalCYNew = payload.RST_CNT;
    },
    [FETCH_CYNEWS_FAILED]: function () {
        return router.push('/home');
    },
    [FETCH_CYNEW_DETAIL]: function (state) {
        state.CYNewDetail = {}
    },
    [FETCH_CYNEW_SUCCESS]: function (state, payload) {
        state.CYNewDetail = payload;
    },
    [FETCH_CYNEW_FAILED]: function () {
        return router.push('/cynews');
    },
}