/**
 * Call api or async inside Actions because Mutation only accept sync works
 */
import i18n from '@/i18n';
import {DO_LOADING, STOP_LOADING, SHOW_POPUP, HIDE_POPUP, SHOW_POPUP_BUTTON, HIDE_POPUP_BUTTON} from '@/mutations/Common.mutation';

export default {
    [DO_LOADING]: async ({commit}) => {
        commit(DO_LOADING);
    },
    [STOP_LOADING]: async ({commit}) => {
        commit(STOP_LOADING);
    },
    [SHOW_POPUP]: async ({commit}, payload) => {
        commit(SHOW_POPUP, payload);
    },
    [HIDE_POPUP]: async ({commit}) => {
        commit(HIDE_POPUP);
    },
    [SHOW_POPUP_BUTTON]: async ({commit, dispatch}, content) => {
        let payload = {
            content,
            actions: [{
                name: i18n.t('Common.OK'),
                isDefault: false,
                callback: () => dispatch(HIDE_POPUP_BUTTON),
                order: 1
            }]
        };
        commit(SHOW_POPUP, payload);
    },
    [HIDE_POPUP_BUTTON]: async ({commit}) => {
        commit(HIDE_POPUP);
    },
}