import router from '@/router'

export const CREATE_NOTICES = 'CREATE_NOTICES';
export const FETCH_NOTICES = 'FETCH_NOTICES';
export const FETCH_NOTICES_SUCCESS = 'FETCH_NOTICES_SUCCESS';
export const FETCH_NOTICES_FAILED = 'FETCH_NOTICES_FAILED';

export const FETCH_NOTICE_DETAIL = 'FETCH_NOTICE_DETAIL';
export const FETCH_NOTICE_SUCCESS = 'FETCH_NOTICE_SUCCESS';
export const FETCH_NOTICE_FAILED = 'FETCH_NOTICE_FAILED';

export const NoticeMutation = {
    [CREATE_NOTICES]: function (state) {
        state.notices = [];
    },
    [FETCH_NOTICES_SUCCESS]: function (state, payload) {
        state.notices = [...state.notices, ...payload.RST_DATA];
        state.totalNotice = payload.RST_CNT;
    },
    [FETCH_NOTICES_FAILED]: function () {
        return router.push('/home');
    },
    [FETCH_NOTICE_DETAIL]: function (state) {
        state.noticeDetail = {}
    },
    [FETCH_NOTICE_SUCCESS]: function (state, payload) {
        state.noticeDetail = payload;
    },
    [FETCH_NOTICE_FAILED]: function () {
        return router.push('/notices');
    },
}