import router from '@/router'

export const CREATE_APPLY_LIST = 'CREATE_APPLY_LIST';
export const RESET_APPLY_LIST = 'RESET_APPLY_LIST';
export const FETCH_APPLY_LIST = 'FETCH_APPLY_LIST';
export const FETCH_APPLY_LIST_SUCCESS = 'FETCH_APPLY_LIST_SUCCESS';
export const FETCH_APPLY_LIST_FAILED = 'FETCH_APPLY_LIST_FAILED';

export const FETCH_APPLY_DETAIL = 'FETCH_APPLY_DETAIL';
export const FETCH_APPLY_DETAIL_SUCCESS = 'FETCH_APPLY_DETAIL_SUCCESS';
export const FETCH_APPLY_DETAIL_FAILED = 'FETCH_APPLY_DETAIL_FAILED';

export const CREATE_CONTACT_LIST = 'CREATE_CONTACT_LIST';
export const RESET_CONTACT_LIST = 'RESET_CONTACT_LIST';
export const FETCH_CONTACT_LIST = 'FETCH_CONTACT_LIST';
export const FETCH_CONTACT_LIST_SUCCESS = 'FETCH_CONTACT_LIST_SUCCESS';
export const FETCH_CONTACT_LIST_FAILED = 'FETCH_CONTACT_LIST_FAILED';

export const FETCH_CONTACT_DETAIL = 'FETCH_CONTACT_DETAIL';
export const FETCH_CONTACT_DETAIL_SUCCESS = 'FETCH_CONTACT_DETAIL_SUCCESS';
export const FETCH_CONTACT_DETAIL_FAILED = 'FETCH_CONTACT_DETAIL_FAILED';

export const FETCH_MY_CAREER = 'FETCH_MY_CAREER';
export const FETCH_MY_CAREER_SUCCESS = 'FETCH_MY_CAREER_SUCCESS';
export const FETCH_MY_CAREER_FAILED = 'FETCH_MY_CAREER_FAILED';

export const CHANGE_LANGUAGE_SKILL = 'CHANGE_LANGUAGE_SKILL';
export const CHANGE_LANGUAGE_SKILL_SUCCESS = 'CHANGE_LANGUAGE_SKILL_SUCCESS';
export const CHANGE_LANGUAGE_SKILL_FAILED = 'CHANGE_LANGUAGE_SKILL_FAILED';

export const UPDATE_PERSONAL_INFO = 'UPDATE_PERSONAL_INFO';
export const UPDATE_PERSONAL_INFO_SUCCESS = 'UPDATE_PERSONAL_INFO_SUCCESS';
export const UPDATE_PERSONAL_INFO_FAILED = 'UPDATE_PERSONAL_INFO_FAILED';

export const MyPageMutation = {
    // for apply list
    [CREATE_APPLY_LIST]: function (state) {
        state.applies = [];
        state.totalApply = 0;
    },
    [RESET_APPLY_LIST]: function (state) {
        state.applies = [];
        state.totalApply = 0;
    },
    [FETCH_APPLY_LIST_SUCCESS]: function (state, payload) {
        state.applies = [...state.applies, ...payload.RST_DATA];
        state.totalApply = payload.RST_CNT;
    },
    [FETCH_APPLY_LIST_FAILED]: function () {
        return router.push('/home');
    },

    // for contact list
    [CREATE_CONTACT_LIST]: function (state) {
        state.contacts = [];
        state.totalContact = 0;
    },
    [RESET_CONTACT_LIST]: function (state) {
        state.contacts = [];
        state.totalContact = 0;
    },
    [FETCH_CONTACT_LIST_SUCCESS]: function (state, payload) {
        state.contacts = [...state.contacts, ...payload.RST_DATA];
        state.totalContact = payload.RST_CNT;
    },
    [FETCH_CONTACT_LIST_FAILED]: function () {
        return router.push('/home');
    },

    // for my career
    [FETCH_MY_CAREER_FAILED]: function () {
        return router.push('/home');
    },
    [FETCH_MY_CAREER_SUCCESS]: function (state, payload) {
        state.myCareer = payload;
    },
    [CHANGE_LANGUAGE_SKILL_SUCCESS]: function (state, payload) {
        state.myCareer = { ...state.myCareer, ...payload }
        
        return router.go(-1);
    },
    [CHANGE_LANGUAGE_SKILL_FAILED]: function () {
        return;
    },

    // for personal info
    [UPDATE_PERSONAL_INFO_SUCCESS]: function (state, payload) {
        state.myCareer = { ...state.myCareer, ...payload }
        
        return router.go(-1);
    },
    [UPDATE_PERSONAL_INFO_FAILED]: function () {
        return;
    }
}