<template>
<div id="app">
    <router-view />
    <Popup />
    <Loading v-if="isLoading" />
</div>
</template>

<script>
window.isDebug = (process.env.NODE_ENV == 'development');

import {
    mapState
} from 'vuex'
import Loading from '@/components/Common/Loading/Loading';
import Popup from '@/components/Common/Popup/Popup';
import {
    LOGIN
} from '@/mutations/Auth.mutation';
import _ from 'lodash';

export default {
    name: 'app',
    components: {
        Loading,
        Popup,
    },
    mounted: function () {
        // if (_.isEmpty(this.$store.state.Auth.user)) {
        //     this.$store.dispatch(LOGIN);
        // }
    },
    created: function () {
        this.$i18n.locale = this.$i18n.locale.toLowerCase();
        this.$validator.localize(this.$i18n.locale.toLowerCase());
    },
    methods: {
        setMetaView() {
            let viewheight = window.innerHeight;
            let viewwidth = window.innerWidth;
            let viewport = document.querySelector("meta[name=viewport]");
            viewport.setAttribute("content", "height=" + viewheight + "px, width=" + viewwidth + "px, initial-scale=1.0");
        }
    },
    computed: mapState({
        isLoading: function (state) {
            return state.Common.isLoading
        }
    })
}
</script>

<style lang="scss">
@import "./App.scss";
</style>
