import router from '@/router';
/**
 * Defined mutations here
 * ex: export const SOME_MUTATION = 'SOME_MUTATION'
 */

export const LOGIN = 'LOGIN';
export const LOGIN_SUCCESS = 'LOGIN_SUCCESS';
export const LOGIN_FAILED = 'LOGIN_FAILED';

export const LOGOUT = 'LOGOUT';
export const LOGOUT_SUCCESS = 'LOGOUT_SUCCESS';
export const LOGOUT_FAILED = 'LOGOUT_FAILED';

export const FIND_ID = 'FIND_ID';
export const FIND_ID_SUCCESS = 'FIND_ID_SUCCESS';
export const FIND_ID_FAILED = 'FIND_ID_FAILED';

export const FIND_PW = 'FIND_PW';
export const FIND_PW_SUCCESS = 'FIND_PW_SUCCESS';
export const FIND_PW_FAILED = 'FIND_PW_FAILED';

export const CHECK_DUP = 'CHECK_DUP';
export const CHECK_DUP_SUCCESS = 'CHECK_DUP_SUCCESS';
export const CHECK_DUP_FAILED = 'CHECK_DUP_FAILED';

export const CHECK_CERT = 'CHECK_CERT';
export const CHECK_CERT_SUCCESS = 'CHECK_CERT_SUCCESS';
export const CHECK_CERT_FAILED = 'CHECK_CERT_FAILED';

export const CHANGE_PASSWORD = 'CHANGE_PASSWORD';
export const CHANGE_PASSWORD_SUCCESS = 'CHANGE_PASSWORD_SUCCESS';
export const CHANGE_PASSWORD_FAILED = 'CHANGE_PASSWORD_FAILED';

/**
 * Defined mutations handlers here
 * @Important mutations HAVE TO BE SYNCHRONOUS
 * 
 */

export const AuthMutation = {
    [LOGIN_SUCCESS]: function (state, payload = {}) {
        state.user = payload
        return router.push('/home');
    },
    [LOGIN_FAILED]: function () {
        return router.push('/auth/login');
    },
    [FIND_ID_SUCCESS]: function (state, payload = {}) {
        state.findIdObject = { ...payload, isSuccess: true }
        return state;
    },
    [FIND_ID_FAILED]: function (state, payload = {}) {
        state.findIdObject = { ...payload }
        return state;
    },
    [LOGOUT_SUCCESS]: function (state) {
        state.user = {}
        return router.push('/auth/login');
    },
    [LOGOUT_FAILED]: function () {
        return;
    },
    [FIND_PW_SUCCESS]: function (state, payload = {}) {
        state.findPwObject = { ...payload, isSuccess: true }
        return state;
    },
    [FIND_PW_FAILED]: function (state, payload = {}) {
        state.findPwObject = { ...payload }
        return state;
    },
    [CHECK_DUP_SUCCESS]: function (state, payload = {}) {
        state.checkDupObject = { ...payload, isSuccess: true }
        return state;
    },
    [CHECK_DUP_FAILED]: function (state, payload = {}) {
        state.checkDupObject = { ...payload }
        return state;
    },
    [CHANGE_PASSWORD_SUCCESS]: function (state, payload) {
        state.user = { ...payload }
        return state;
    },
    [CHANGE_PASSWORD_FAILED]: function () {
        return;
    },
}
