    CREATE_APPLY_LIST,
    RESET_APPLY_LIST,
    FETCH_APPLY_LIST,
    FETCH_APPLY_LIST_SUCCESS,
    FETCH_APPLY_LIST_FAILED,

    FETCH_APPLY_DETAIL,
    FETCH_APPLY_DETAIL_SUCCESS,
    FETCH_APPLY_DETAIL_FAILED,

    CREATE_CONTACT_LIST,
    RESET_CONTACT_LIST,
    FETCH_CONTACT_LIST,
    FETCH_CONTACT_LIST_SUCCESS,
    FETCH_CONTACT_LIST_FAILED,

    FETCH_CONTACT_DETAIL,
    FETCH_CONTACT_DETAIL_SUCCESS,
    FETCH_CONTACT_DETAIL_FAILED,

