/**
 * Defined mutations here
 * ex: export const SOME_MUTATION = 'SOME_MUTATION'
 */

 export const DO_LOADING = 'DO_LOADING';
 export const STOP_LOADING = 'STOP_LOADING';
 export const SHOW_POPUP = 'SHOW_POPUP';
 export const HIDE_POPUP = 'HIDE_POPUP';
 export const SHOW_POPUP_BUTTON = 'SHOW_POPUP_BUTTON';
 export const HIDE_POPUP_BUTTON = 'HIDE_POPUP_BUTTON';
 export const SHOW_POPUP_BUTTONS = 'SHOW_POPUP_BUTTONS';
 export const HIDE_POPUP_BUTTONS = 'HIDE_POPUP_BUTTONS';

/**
 * Defined mutations handlers here
 * @Important mutations HAVE TO BE SYNCHRONOUS
 * 
 */

 export const CommonMutation = {
    [DO_LOADING] (state) {
        return state.isLoading = true;
    },
    [STOP_LOADING] (state) {
        return state.isLoading = false;
    },
    [SHOW_POPUP] (state, payload) {
        state.Popup.isShow = true;
        state.Popup.title = payload.title;
        state.Popup.content = payload.content;
        state.Popup.actions = payload.actions;
        return state;
    },
    [HIDE_POPUP] (state) {
        return state.Popup.isShow = false;
    },
 }
