import api from '@/services/api.service'
import { RESPONSE_CODE, RESPONSE_STATUS } from '@/constants'
import i18n from '@/i18n'
import {
    CREATE_PROJECTS, FETCH_PROJECTS, FETCH_PROJECTS_SUCCESS, FETCH_PROJECTS_FAILED, FETCH_PROJECT_DETAIL, FETCH_PROJECT_SUCCESS, FETCH_PROJECT_FAILED, APPLY_PROJECT_REQUEST, APPLY_PROJECT_SUCCESS, APPLY_PROJECT_FAILED
} from '@/mutations/Project.mutation';

export default {
    [FETCH_PROJECTS]: async ({ commit }, data = {}) => {
        let res = await api.getProjects(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                commit(FETCH_PROJECTS_SUCCESS, res.data);
            } else {
                commit(FETCH_PROJECTS_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(FETCH_PROJECTS_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
    [CREATE_PROJECTS]: async ({ commit }) => {
        commit(CREATE_PROJECTS);
    },
    [FETCH_PROJECT_DETAIL]: async ({ commit }, data = {}) => {
        let res = await api.getProjectDetails(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                commit(FETCH_PROJECT_SUCCESS, res.data);
            } else {
                commit(FETCH_PROJECT_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(FETCH_PROJECT_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
    [APPLY_PROJECT_REQUEST]: async ({ commit }, data = {}) => {
        let res = await api.applyProject(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                // commit(FETCH_PROJECT_SUCCESS, res.data);
                return res.data.RST_DATA
            } else {
                // commit(FETCH_PROJECT_FAILED, res.data);
                // if (res && res.data && res.data.RST_MSG) {
                    // throw new Error(res.data.RST_MSG);
                // }
                return false
            }
        } else {
            commit(FETCH_PROJECT_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    }
}