import Vue from 'vue';
import Router from 'vue-router';

// Lazy loading for component and chunkalize
const SplashContainer = () => import('@/containers/SplashContainer');
const LoginContainer = () => import('@/containers/LoginContainer');
const FindPasswordOrId = () => import('@/containers/FindPasswordOrId');
const JoinStepOne = () => import('@/containers/JoinStepOne');
const JoinStepTwo = () => import('@/containers/JoinStepTwo');
const PersonalInformation = () => import('@/containers/PersonalInformation');
const HomeContainer = () => import('@/containers/HomeContainer');
const ProjectList = () => import('@/containers/ProjectList');
const ProjectDetails = () => import('@/containers/ProjectDetailsContainer');
const LanguageSkillsContainer = () => import('@/containers/LanguageSkillsContainer');
const BasicInformationContainer = () => import('@/containers/BasicInformationContainer');
const Notice = () => import('@/containers/Notice');
const NoticeDetail = () => import('@/components/Notice/NoticeDetail');
const CYNews = () => import('@/containers/CYNews');
const CYNewDetail = () => import('@/components/CYNew/CYNewDetail');
const PartnerNews = () => import('@/containers/PartnerNews');
const PartnerNewDetail = () => import('@/components/PartnerNew/PartnerNewDetail');
const Notification = () => import('@/containers/Notification');
const NotificationDetail = () => import('@/components/Notification/NotificationDetail');
const MyPageContainer = () => import('@/containers/MyPageContainer');
const Setting = () => import('@/containers/Setting');
const ChangePassword = () => import('@/containers/ChangePasswordContainer');

Vue.use(Router);

const router = new Router({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/splash',
            name: 'SplashContainer',
            component: SplashContainer
        },
        {
            path: '/auth/login',
            name: 'LoginContainer',
            component: LoginContainer
        },
        {
            path: '/auth/find-password-or-id',
            name: 'FindPasswordOrId',
            component: FindPasswordOrId
        },
        {
            path: '/joining/step-one',
            name: 'JoinStepOne',
            component: JoinStepOne
        },
        {
            path: '/joining/step-two',
            name: 'JoinStepTwo',
            component: JoinStepTwo
        },
        {
            path: '/personal-information',
            name: 'PersonalInformation',
            component: PersonalInformation
        },
        {
            path: '/home',
            name: 'HomeContainer',
            component: HomeContainer
        },
        {
            path: '/projects',
            name: 'ProjectList',
            component: ProjectList
        },
        {
            path: '/project/:id',
            name: 'ProjectDetails',
            component: ProjectDetails
        },
        {
            path: '/language-skills',
            name: 'LanguageSkillsContainer',
            component: LanguageSkillsContainer,
            props: true
        },
        {
            path: '/basic-information',
            name: 'BasicInformationContainer',
            component: BasicInformationContainer
        },
        {
            path: '/notifications',
            name: 'Notifications',
            component: Notification
        },
        {
            path: '/notification-detail',
            name: 'NotificationDetail',
            component: NotificationDetail,
            props: true
        },
        {
            path: '/notices',
            name: 'Notices',
            component: Notice
        },
        {
            path: '/notice/:id',
            name: 'NoticeDetail',
            component: NoticeDetail,
            props: true
        },
        {
            path: '/cynews',
            name: 'CYNews',
            component: CYNews
        },
        {
            path: '/cynew/:id',
            name: 'CYNewDetail',
            component: CYNewDetail,
            props: true
        },
        {
            path: '/partner-news',
            name: 'PartnerNews',
            component: PartnerNews
        },
        {
            path: '/partner-new/:id',
            name: 'PartnerNewDetail',
            component: PartnerNewDetail,
            props: true
        },
        {
            path: '/my-page/:tab',
            name: 'MyPageContainer',
            component: MyPageContainer,
            props: true
        },
        {
            path: '/settings',
            name: 'Setting',
            component: Setting
        },
        {
            path: '/change-password',
            name: 'ChangePassword',
            component: ChangePassword
        },
        {
            path: '/*',
            redirect: '/splash'
        },
    ],
});

router.beforeEach((to, from, next) => {
    let user = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : null;
    if (!user) {
        if (to.path !== '/splash') {
            let ignorePath = ['/auth/login', '/splash', '/joining/step-one', '/joining/step-two', '/home', '/auth/find-password-or-id'];
            if (!ignorePath.includes(from.path)) {
                next({ path: '/splash' })
            }
        }
        next()
    }

    else if (user && to.path !== '/auth/login' || to.path == '/splash' || from.path == '/splash') {
        next()
    }
    else {
        next(false)
    }
});

router.afterEach(() => {
    //console.log(`STOP COMPONENT LOADING`)
});

export default router;