import router from '@/router'

export const CREATE_PARTNER_NEWS = 'CREATE_PARTNER_NEWS';
export const FETCH_PARTNER_NEWS = 'FETCH_PARTNER_NEWS';
export const FETCH_PARTNER_NEWS_SUCCESS = 'FETCH_PARTNER_NEWS_SUCCESS';
export const FETCH_PARTNER_NEWS_FAILED = 'FETCH_PARTNER_NEWS_FAILED';

export const FETCH_PARTNER_NEW_DETAIL = 'FETCH_PARTNER_NEW_DETAIL';
export const FETCH_PARTNER_NEW_SUCCESS = 'FETCH_PARTNER_NEW_SUCCESS';
export const FETCH_PARTNER_NEW_FAILED = 'FETCH_PARTNER_NEW_FAILED';

export const PartnerNewMutation = {
    [CREATE_PARTNER_NEWS]: function (state) {
        state.partnerNews = [];
    },
    [FETCH_PARTNER_NEWS_SUCCESS]: function (state, payload) {
        state.partnerNews = [...state.partnerNews, ...payload.RST_DATA];
        state.totalPartnerNews = payload.RST_CNT;
    },
    [FETCH_PARTNER_NEWS_FAILED]: function () {
        return router.push('/home');
    },
    [FETCH_PARTNER_NEW_DETAIL]: function (state) {
        state.partnerNewDetail = {}
    },
    [FETCH_PARTNER_NEW_SUCCESS]: function (state, payload) {
        state.partnerNewDetail = payload;
    },
    [FETCH_PARTNER_NEW_FAILED]: function () {
        return router.push('/partner-news');
    },
}