import axios from 'axios';
import store from '@/store';
import { DO_LOADING, STOP_LOADING } from '@/mutations/Common.mutation';
import { DO_UPLOAD_ON_PROGRESS } from '@/mutations/Upload.mutation';

export default {
    install: function (Vue) {

        this.axios = axios.create({
            baseURL: `${process.env.VUE_APP_API_URL}`,
            timeout: 10000
        });
        this.axios.interceptors.request.use(function (req) {
            return req;
        });
        this.axios.interceptors.response.use(function (res) {
            store.dispatch(STOP_LOADING);
            return res;
        }, function (err) {
            store.dispatch(STOP_LOADING);
            return err;
        });

        Vue.prototype.$ApiService = this;
    },
    doPost: function (url, data, loading = true) {
        if (loading) {
            store.dispatch(DO_LOADING);
        }

        const token = localStorage.getItem('access_token') || process.env.VUE_APP_API_TOKEN;
        return this.axios.post(url, data, {
            headers: { 'Authorization': `Basic ${token}` }
        })
    },
    doGet: function (url, data) {
        store.dispatch(DO_LOADING);
        const token = localStorage.getItem('access_token') || process.env.VUE_APP_API_TOKEN;
        return this.axios.get(url, {
            headers: { 'Authorization': `Basic ${token}` },
            params: data
        })
    },
    doLogin: function (data) {
        return this.doPost('/user/doMLogin', data);
    },
    doFindId: function (data) {
        return this.doPost('/user/findUserId', data);
    },
    doFindPw: function (data) {
        return this.doPost('/user/findPw', data);
    },
    doCheckDup: function (data) {
        return this.doPost('/user/duplicationIDCheck', data);
    },
    doCheckCert: function (data) {
        return this.doPost('/user/checkCertEmail', data);
    },

    getProjects: function (data) {
        return this.doPost('/proj/getListData', data);
    },
    getProjectDetails: function (data) {
        return this.doPost('/proj/getDetailData', data);
    },
    getBBSList: function (data) {
        return this.doPost('/bbs/getListData', data);
    },
    getBBSDetail: function (data) {
        return this.doPost('/bbs/getDetailData', data);
    },
    applyProject: function (data) {
        return this.doPost('/proj/apply', data);
    },

    doUploadFile: function (files = []) {
        const token = localStorage.getItem('access_token') || process.env.VUE_APP_API_TOKEN;
        var formData = new FormData();
        formData.append("uploadFile", files[0]);
        store.dispatch(DO_LOADING);
        return this.axios.post('/file/upload', formData, {
            headers: {
                'Authorization': `Basic ${token}`,
                'Content-Type': 'multipart/form-data'
            },
            onUploadProgress: function (progressEvent) {
                var percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
                store.commit(DO_UPLOAD_ON_PROGRESS, percentCompleted)
            }
        });
    },

    changePwd: function (data) {
        return this.doPost('/user/setChangePw', data)
    },

    doGetCommonInfo: function (key) {
        return this.doPost('/comm/commonInfo', { upCmmCode: key });
    },

    saveLS: function (data) {
        return this.doPost('/sp/saveLS', data)
    },

    getApplyList: function (data) {
        return this.doPost('/sp/getApplyList', data)
    },

    getContactList: function (data) {
        return this.doPost('/sp/getContactList', data)
    },

    getMyCareer: function (data) {
        return this.doPost('/sp/getMyCareerData', data)
    },

    updatePersonalInfo: function (data) {
        return this.doPost('/sp/savePI', data)
    }
};
