import api from '@/services/api.service'
import { RESPONSE_CODE, RESPONSE_STATUS } from '@/constants'
import i18n from '@/i18n'
import {
    CREATE_NOTICES, FETCH_NOTICES, FETCH_NOTICES_SUCCESS, FETCH_NOTICES_FAILED, FETCH_NOTICE_DETAIL, FETCH_NOTICE_SUCCESS, FETCH_NOTICE_FAILED
} from '@/mutations/Notice.mutation';

export default {
    [FETCH_NOTICES]: async ({ commit }, data = {}) => {
        let res = await api.getBBSList(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                commit(FETCH_NOTICES_SUCCESS, res.data);
            } else {
                commit(FETCH_NOTICES_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(FETCH_NOTICES_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
    [CREATE_NOTICES]: async ({ commit }) => {
        commit(CREATE_NOTICES);
    },
    [FETCH_NOTICE_DETAIL]: async ({ commit }, data = {}) => {
        commit(FETCH_NOTICE_DETAIL);
        let res = await api.getBBSDetail(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                commit(FETCH_NOTICE_SUCCESS, res.data.RST_DATA[0]);
            } else {
                commit(FETCH_NOTICES_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(FETCH_NOTICE_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
}