/**
 * Vue i18n
 *
 * @library
 *
 * http://kazupon.github.io/vue-i18n/en/
 */

// Lib imports
import Vue from 'vue'
import VueI18n from 'vue-i18n'
import lang from '@/lang'

Vue.use(VueI18n)
const locale = window.localStorage.getItem('user') ? window.localStorage.getItem('locale') : 'kr'
const messages = lang.messages

const i18n = new VueI18n({
  locale: locale,
  fallbackLocale: 'en',
  messages
})

