import router from '@/router'

export const CREATE_PROJECTS = 'CREATE_PROJECTS';
export const FETCH_PROJECTS = 'FETCH_PROJECTS';
export const FETCH_PROJECTS_SUCCESS = 'FETCH_PROJECTS_SUCCESS';
export const FETCH_PROJECTS_FAILED = 'FETCH_PROJECTS_FAILED';

export const FETCH_PROJECT_DETAIL = 'FETCH_PROJECT_DETAIL';
export const FETCH_PROJECT_SUCCESS = 'FETCH_PROJECT_SUCCESS';
export const FETCH_PROJECT_FAILED = 'FETCH_PROJECT_FAILED';

export const APPLY_PROJECT_REQUEST = 'APPLY_PROJECT_REQUEST';
export const APPLY_PROJECT_SUCCESS = 'APPLY_PROJECT_SUCCESS';
export const APPLY_PROJECT_FAILED = 'APPLY_PROJECT_FAILED';

export const ProjectMutation = {
    [CREATE_PROJECTS]: function(state) {
        state.projects = [];
        state.totalProject = 0;
    },
    [FETCH_PROJECTS_SUCCESS]: function (state, payload) {
        state.projects = [...state.projects, ...payload.RST_DATA];
        state.totalProject = payload.RST_CNT;
    },
    [FETCH_PROJECTS_FAILED]: function () {
        return router.push('/home');
    },
    [FETCH_PROJECT_SUCCESS]: function (state, payload) {
        state.projectDetail = payload.RST_DATA;
    },
    [FETCH_PROJECT_FAILED]: function () {
        return router.push('/home');
    }
}