import api from '@/services/api.service'
import { RESPONSE_CODE, RESPONSE_STATUS } from '@/constants'
import i18n from '@/i18n'
import {
    CREATE_CYNEWS, FETCH_CYNEWS, FETCH_CYNEWS_SUCCESS, FETCH_CYNEWS_FAILED, FETCH_CYNEW_DETAIL, FETCH_CYNEW_SUCCESS, FETCH_CYNEW_FAILED
} from '@/mutations/CYNew.mutation';

export default {
    [FETCH_CYNEWS]: async ({ commit }, data = {}) => {
        let res = await api.getBBSList(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                commit(FETCH_CYNEWS_SUCCESS, res.data);
            } else {
                commit(FETCH_CYNEWS_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(FETCH_CYNEWS_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
    [CREATE_CYNEWS]: async ({ commit }) => {
        commit(CREATE_CYNEWS);
    },
    [FETCH_CYNEW_DETAIL]: async ({ commit }, data = {}) => {
        commit(FETCH_CYNEW_DETAIL);
        let res = await api.getBBSDetail(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                commit(FETCH_CYNEW_SUCCESS, res.data.RST_DATA[0]);
            } else {
                commit(FETCH_CYNEWS_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(FETCH_CYNEW_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
}