import api from '@/services/api.service'
import { RESPONSE_CODE, RESPONSE_STATUS } from '@/constants'
import i18n from '@/i18n'
import {
    CREATE_PARTNER_NEWS, FETCH_PARTNER_NEWS, FETCH_PARTNER_NEWS_SUCCESS, FETCH_PARTNER_NEWS_FAILED, FETCH_PARTNER_NEW_DETAIL, FETCH_PARTNER_NEW_SUCCESS, FETCH_PARTNER_NEW_FAILED
} from '@/mutations/PartnerNew.mutation';

export default {
    [FETCH_PARTNER_NEWS]: async ({ commit }, data = {}) => {
        let res = await api.getBBSList(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                commit(FETCH_PARTNER_NEWS_SUCCESS, res.data);
            } else {
                commit(FETCH_PARTNER_NEWS_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(FETCH_PARTNER_NEWS_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
    [CREATE_PARTNER_NEWS]: async ({ commit }) => {
        commit(CREATE_PARTNER_NEWS);
    },
    [FETCH_PARTNER_NEW_DETAIL]: async ({ commit }, data = {}) => {
        commit(FETCH_PARTNER_NEW_DETAIL);
        let res = await api.getBBSDetail(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                commit(FETCH_PARTNER_NEW_SUCCESS, res.data.RST_DATA[0]);
            } else {
                commit(FETCH_PARTNER_NEWS_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(FETCH_PARTNER_NEW_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
}