/**
 * Defined mutations here
 * ex: export const SOME_MUTATION = 'SOME_MUTATION'
 */

export const DO_UPLOAD = 'DO_UPLOAD';
export const DO_UPLOAD_SUCCESS = 'DO_UPLOAD_SUCCESS';
export const DO_UPLOAD_FAILED = 'DO_UPLOAD_FAILED';
export const DO_UPLOAD_ON_PROGRESS = 'DO_UPLOAD_ON_PROGRESS';
export const DO_UPLOAD_RESET = 'DO_UPLOAD_RESET';

/**
* Defined mutations handlers here
* @Important mutations HAVE TO BE SYNCHRONOUS
* 
*/

export const UploadMutation = {
    [DO_UPLOAD](state) {
        state.isUploading = true;
        return state;
    },
    [DO_UPLOAD_SUCCESS](state, payload) {
        state.isSuccess = true;
        state.isUploading = false;
        state.uploadedPath = payload;
        return state;
    },
    [DO_UPLOAD_FAILED](state, payload) {
        state.isSuccess = false;
        state.isUploading = false;
        state.message = payload;
        return state;
    },
    [DO_UPLOAD_ON_PROGRESS](state, payload) {
        state.progress = payload;
        return state;
    },
    [DO_UPLOAD_RESET](state) {
        state.progress = 0;
        state.isUploading = false;
        state.isSuccess = false;
        state.currentFile = undefined;
        state.uploadedPath = '';
        state.message = '';
        return state;
    },
    
}
