import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate'

import { AuthStore, CommonStore, ProjectStore, NoticeStore, CYNewStore, PartnerNewStore, UploadStore, MyPageStore } from '@/stores';

Vue.use(Vuex);

const store = new Vuex.Store({
    modules: {
        Auth: AuthStore,
        Common: CommonStore,
        Project: ProjectStore,
        Notice: NoticeStore,
        Upload: UploadStore,
        CYNew: CYNewStore,
        PartnerNew: PartnerNewStore,
        MyPage: MyPageStore
    },
    plugins: [createPersistedState({
        paths: ['Auth', 'Project', 'Notice', 'Upload', 'CYNew', 'PartnerNew', 'MyPage']
    })]
})

export default store;