import api from '@/services/api.service';
import storeService from '@/services/store.service';
import {RESPONSE_CODE, RESPONSE_STATUS} from '@/constants';
import i18n from '@/i18n';
/**
 * Call api or async inside Actions because Mutation only accept sync works
 */

import {
    LOGIN, LOGIN_SUCCESS, LOGIN_FAILED, 
    LOGOUT, LOGOUT_SUCCESS, LOGOUT_FAILED, 
    FIND_PW, FIND_PW_SUCCESS, FIND_PW_FAILED, 
    FIND_ID, FIND_ID_SUCCESS, FIND_ID_FAILED,
    CHECK_CERT, CHECK_CERT_SUCCESS, CHECK_CERT_FAILED,
    CHECK_DUP, CHECK_DUP_SUCCESS, CHECK_DUP_FAILED,
    CHANGE_PASSWORD, CHANGE_PASSWORD_SUCCESS, CHANGE_PASSWORD_FAILED
} from '@/mutations/Auth.mutation';

export default {
    [LOGIN]: async ({commit}, data = {}) => {
        let res = await api.doLogin(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                // Check initPw
                commit(LOGIN_SUCCESS, res.data.RST_DATA);
                storeService.set('user', JSON.stringify(res.data.RST_DATA));
            } else {
                commit(LOGIN_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(LOGIN_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
    [LOGOUT]: async ({commit}) => {
        try {
            storeService.set('user', '');
            commit(LOGOUT_SUCCESS);
        } catch(err) {
            commit(LOGOUT_FAILED);
        }
    },
    [FIND_ID]: async ({commit}, data = {}) => {
        let res = await api.doFindId(data);
        if (res && res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
            commit(FIND_ID_SUCCESS, res.data);
        } else {
            commit(FIND_ID_FAILED, res.data);
        }
    },
    [FIND_PW]: async ({commit}, data = {}) => {
        let res = await api.doFindPw(data);
        if (res && res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
            commit(FIND_PW_SUCCESS, res.data);
        } else {
            commit(FIND_PW_FAILED, res.data);
        }
    },
    [CHECK_DUP]: async ({commit}, data = {}) => {
        let res = await api.doCheckDup(data);
        if (res && res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
            commit(CHECK_DUP_SUCCESS, res.data);
            if (res.data.RST_DATA) {
                throw new Error(i18n.t('Joining.Enteredmailhasbeenused'));
            } else {
                throw new Error(i18n.t('Joining.Emailisvalid'));
            }
        } else {
            commit(CHECK_DUP_FAILED, res.data);
        }
    },
    [CHECK_CERT]: async ({commit}, data = {}) => {
        let res = await api.doCheckCert(data);
        if (res && res.data && res.data.resultCode === RESPONSE_STATUS.S) {
            return true;
        } else {
            throw new Error(i18n.t('Joining.Confirmcodeisinvalid'));
            //commit(CHECK_CERT_FAILED, res.data);
        }
    },
    [CHANGE_PASSWORD]: async ({commit}, data = {}) => {
        let res = await api.changePwd(data);
        if (res.status === RESPONSE_CODE.OK) {
            if (res.data && res.data.RST_CD === RESPONSE_STATUS.S) {
                // Check initPw
                commit(CHANGE_PASSWORD_SUCCESS, res.data.RST_DATA);
                storeService.set('user', JSON.stringify(res.data.RST_DATA));
            } else {
                commit(CHANGE_PASSWORD_FAILED, res.data);
                if (res && res.data && res.data.RST_MSG) {
                    throw new Error(res.data.RST_MSG);
                }
            }
        } else {
            commit(CHANGE_PASSWORD_FAILED, res.data);
            throw new Error(i18n.t('Login.Somethingwentwrong'));
        }
    },
}